.shopcategory-banner{
    /* display: block;
    margin: 30px auto;
    width:82% ; */
    margin: 20px 120px;
    display: grid;
    grid-template-columns:1fr 1fr 1fr 1fr;
    row-gap: 80px;
    width: 82%;
}
.shopcategory-indexSort{
    display: flex;
    margin: 0px 170px;
    justify-content: space-between;
    align-items: center;
}
.shopcategory-sort{
    padding: 10px 20px;
    border-radius: 40px;
    border: 1px solid #888 ;
    /* display: flex;
    justify-content: flex-end; */
}

.shopcategory-indexSort p span{
    font-weight: 600;
}
.shopcategory-products{
    margin: 20px 120px;
    display: grid;
    grid-template-columns:1fr 1fr 1fr 1fr;
    row-gap: 80px;
}

.shopcategory-loadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 100px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background: #ededed;
    color: #787878;
    font-size: 18px;
    font-weight: 500;
}